import {
  ArrowDownRightIcon,
  ChevronLeftIcon,
  ChevronRightIcon,
  PencilIcon,
  TrashIcon,
} from "@heroicons/react/24/outline";
import { Button, Modal } from "antd";
import { data } from "autoprefixer";
import * as React from "react";
import { ResizableBox } from "react-resizable";
import config from "../../../../gatsby-config";
import { IGeneratorConfig } from "../../types";
import CanvasView from "../editor/canvas";
import MaskView from "../editor/mask";

const UploadView = ({
  config,
  setConfig,
}: {
  config: IGeneratorConfig;
  setConfig: (config: IGeneratorConfig) => void;
}) => {
  const [imageRatio, setImageRatio] = React.useState(1);
  const [showModal, setShowModal] = React.useState(false);
  // console.log("initImage", initImage);

  const imageRef = React.useRef<HTMLImageElement>(null);
  const minImageWidth = 400;

  React.useEffect(() => {
    setImageRatio(
      imageRef.current?.naturalHeight / imageRef.current?.naturalWidth
    );
    console.log("initImage change", imageRatio);
  }, [config.init_image]);

  return (
    <div>
      <Modal
        title={<div className="select-none">{"Edit Image"}</div>}
        visible={showModal}
        onOk={() => {
          setShowModal(false);
        }}
        onCancel={() => {
          setShowModal(false);
        }}
        width="min-content"
        footer={null}
      >
        <MaskView
          config={config}
          setConfig={setConfig}
          showModal={showModal}
          setShowModal={setShowModal}
        />
      </Modal>
      <div className=" ">
        {" "}
        <div className="relative w-40  h-40 rounded shadow-lg overflow-hidden  mb-0">
          {config.init_image && (
            <img
              ref={imageRef}
              role="button"
              onClick={() => {
                setShowModal(true);
              }}
              alt="guide image"
              className={`absolute object-cover ${
                config.mask_image ? "opacity-40 " : ""
              }rounded inline-block   top-0 left-0 border`}
              src={config.init_image}
            />
          )}
          {config.mask_image && (
            <img
              ref={imageRef}
              role="button"
              onClick={() => {
                setShowModal(true);
              }}
              // onLoad={() => {
              //   console.log("imageRef");
              // }}
              alt="image mask"
              className="absolute top-0 left-0 object-cover rounded inline-block border"
              src={config.mask_image}
            />
          )}
        </div>
        <div className="text-xs mt-2 text-secondary">
          <span
            onClick={() => {
              setConfig({ ...config, init_image: null, mask_image: null });
            }}
            role={"button"}
            className="hover:text-accent"
          >
            <TrashIcon className="inline-block w-4 h-4 ml-1" /> clear
          </span>{" "}
          |{" "}
          <span
            onClick={() => {
              setShowModal(true);
            }}
            role={"button"}
            className="hover:text-accent"
          >
            <PencilIcon className="inline-block w-4 h-4 ml-1" /> edit
          </span>{" "}
        </div>
      </div>
    </div>
  );
};

export default UploadView;
